const $siteList = $('.siteList')
const $lastLi = $siteList.find('li.last')
const x = localStorage.getItem('x')
const xObject = JSON.parse(x)
const hashMap = xObject || [
  {logo: 'B', url: 'https://www.baidu.com'},
  {logo: 'Z', url: 'https://www.zhihu.com'},
  {logo: 'I', url: 'https://www.iconfont.cn/'},
  {logo: 'Y', url: 'https://www.youtube.com/'},
  {logo: 'G', url: 'https://github.com/'},
  {logo: 'V', url: 'https://www.v2ex.com/'},
  {logo: 'L', url: 'https://www.bilibili.com/'},
  {logo: 'X', url: 'https://www.xiedaimala.com/'},

]
window.onload=()=>{
  if (x===""){
    localStorage.setItem('x', hashMap)
  }
}
const simplifyUrl = (url) => {
  return url.replace('https://', '')
    .replace('http://', '')
    .replace('www.', '')
    .replace(/\/.*/, '') // åˆ é™¤ / å¼€å¤´çš„å†…å®¹
}

const render = () => {
  $siteList.find('li:not(.last)').remove()
  hashMap.forEach((node, index) => {
    const $li = $(`<li>
      <div class="site">
        <div class="logo">${node.logo}</div>
        <div class="link">${simplifyUrl(node.url)}</div>
        <div class="close">
          <svg class="icon">
            <use xlink:href="#icon-close"></use>
          </svg>
        </div>
      </div>
    </li>`).insertBefore($lastLi)
    $li.on('click', () => {
      window.open(node.url)
    })
    $li.on('click', '.close', (e) => {
      e.stopPropagation() // é˜»æ­¢å†’æ³¡
      hashMap.splice(index, 1)
      render()
    })
  })
}

render()

$('.addButton').on('click', () => {
  let url = window.prompt('å¡«å†™ç½‘å€ï¼Ÿ')
  if (url.indexOf('http') !== 0) {
    url = 'https://' + url
  }
  console.log(url)
  hashMap.push({
    logo: simplifyUrl(url)[0].toUpperCase(),
    url: url
  })
  render()
})

window.onbeforeunload = () => {
  const string = JSON.stringify(hashMap)
  localStorage.setItem('x', string)
}

